			this.editor = UE.getEditor('editor', this.config); // åˆå§‹åŒ–UE
			this.editor.addListener("ready", function() {
				_this.editor.setContent(_this.defaultMsg); // ç¡®ä¿UEåŠ è½½å®ŒæˆåŽï¼Œæ”¾å…¥å†…å®¹ã€‚
