<template>
	<div class="login">
		<!--ç™»å½•æ¡†-->
		<div class="login_box">
			<div class="manage_title">llwç®¡ç†åŽå°</div>
			<el-form ref="form" :model="form" label-width="110px" label-position="right" :rules="rules">
				<!--è´¦å·-->
				<el-row>
					<el-col :span="20">
		  				<el-form-item label="è´¦å·" prop="account_number">
		    				<el-input v-model="form.account_number"></el-input>
		  				</el-form-item>
					</el-col>
				</el-row>
				<el-row>
					<el-col :span="20">
		  				<el-form-item label="å¯†ç " prop="password">
		    				<el-input v-model="form.password"></el-input>
		  				</el-form-item>
					</el-col>
				</el-row>
