					password		:	""		,		//å¯†ç 
				},
				rules:{
          			account_number	: [
            			{ required: true, message: 'è¯·è¾“å…¥ç™»å½•è´¦å·', trigger: 'blur' },
          			],
          			password		: [
            			{ required: true, message: 'è¯·è¾“å…¥ç™»å½•å¯†ç ', trigger: 'blur' },
          			],
				},
			}
		},
		methods:{
			/*ç‚¹å‡»ç™»å½•æŒ‰é’®*/
			login_submit(formName){
				var that = this ;
				that.$refs[formName].validate((valid) => {
          			if (valid) {
            			if(that.form.account_number == "admin" && that.form.password == "123456"){
            				that.$message.success('ç™»å½•æˆåŠŸ');
            				that.$router.push({path:"main_page"})
            			}else{
            				that.$message.error('ç™»å½•è´¦å·æˆ–è€…å¯†ç å¡«å†™é”™è¯¯');
            			}
          			} else {
            			console.log('error submit!!');
            			return false;
         			}
        		});
			}
		}
	})
</script>

<style lang="less">
	html,body,#app{
