		height: 100%;
		padding: 0;
		margin: 0;
	}
</style>
<style scoped="scoped" lang="less">
	.login{
		height: 100%;
		width:100%;
		background-image: url(../../../static/images/login.jpg);
		background-size: 100% 100%;
		.login_box{
			height: 400px;
			width: 600px;
			background: #fff;
			position: fixed;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);
			border-radius: 5px;
			.manage_title{
				height: 30px;
				line-height: 30px;
				text-align: center;
				margin: 60px 0 0;
				font-size: 18px;
			}
			form{
